<template>
    <section class='_shopClass'>
        <el-dialog
            title="购课"
            :visible.sync="tablesThis.shopClassShos">
            <section class='body-section'>
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="普通购课" name="0">
                        <section class='el-tab-section el-tab-section-left'>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*科&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;目：</span>
                                <el-select   slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                    <el-option label="男" value="1" ></el-option>
                                    <el-option label="女" value="2" ></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*课&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;程：</span>
                                <el-select   slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                    <el-option label="男" value="1" ></el-option>
                                    <el-option label="女" value="2" ></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*教师资历：</span>
                                <el-select   slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                    <el-option label="男" value="1" ></el-option>
                                    <el-option label="女" value="2" ></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*教&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;师：</span>
                                <el-select   slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                    <el-option label="男" value="1" ></el-option>
                                    <el-option label="女" value="2" ></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*收费标准：</span>
                                <el-input placeholder="填数字" v-model="input2">
                                    <template slot="append">元</template>
                                </el-input>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*购买课时：</span>
                                <el-input placeholder="填数字" v-model="input2">
                                    <template slot="append">元</template>
                                </el-input>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*有 效 期 ：</span>
                                <el-date-picker
                                    v-model="value1"
                                    type="date"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*购课时间：</span>
                                <el-date-picker
                                    v-model="value1"
                                    type="date"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </label>
                            <label class='label-drop-down label-dis-flex'>
                                <span class='label-drop-down-title'>*课程备注：</span>
                                <el-input disabled placeholder="该课程暂无备注"></el-input>
                            </label>
                            <label class='label-drop-down label-dis-flex' style='align-items: end;'>
                                <span class='label-drop-down-title'>*购课备注：</span>
                                <el-input
                                    type="textarea"
                                    placeholder="可填写备注说明"
                                    show-word-limit
                                    >
                                </el-input>
                            </label>
                        </section>
                        <section class='el-tab-section el-tab-section-right'>
                            <label class='el-tab-section-label'>
                                <span class='el-tab-section-label-title'>*优惠方案：</span>
                                <div class='radio_div'>
                                    <label class='radio_section'>
                                        <input type="radio">
                                        <span>暂无优惠</span>
                                    </label>
                                    <label class='radio_section'>
                                        <input type="radio">
                                        <span>折扣优惠</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="填数字" v-model="input2">
                                                <template slot="append">%</template>
                                            </el-input>
                                        </section>
                                    </label>
                                    <label class='radio_section'>
                                        <input type="radio">
                                        <span>金额优惠</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="填数字" v-model="input2">
                                                <template slot="append">%</template>
                                            </el-input>
                                        </section>
                                    </label>
                                    <label class='radio_section'>
                                        <input type="checkbox">
                                        <span>赠课课时</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="填数字" v-model="input2">
                                                <template slot="append">%</template>
                                            </el-input>
                                        </section>
                                    </label>
                                </div>
                            </label>
                            <label class='el-tab-section-label el-tab-margin-left'>
                                <span class='personnel'>录 入 人 ：<em>BEE</em></span>
                                <span class='personnel'>跟 进 人 ：<em>BEE</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left' >
                                <span class='el-tab-section-title'>实 付 款 ：</span>
                                <el-input v-model="input2">
                                    <template slot="append">元</template>
                                </el-input>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left'>
                                <span class='personnel'>账户余额：<em>0元</em></span>
                                <span class='personnel'><em>原价: 0.00元；优惠：0.00元</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append' >
                                <span class='el-tab-section-title'>*付款方式：</span>
                                <el-select   slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                    <el-option label="男" value="1" ></el-option>
                                    <el-option label="女" value="2" ></el-option>
                                </el-select>
                            </label>
                        </section>
                    </el-tab-pane>
                    <el-tab-pane label="套餐购课" name="1">套餐购课</el-tab-pane>
                </el-tabs>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="tablesThis.shopClassShos = false"  size='mini'>确 定</el-button>
                <el-button @click="tablesThis.shopClassShos = false" size='mini'>取 消</el-button>
            </span>
        </el-dialog>
    </section>
</template>
<script>
export default {
    name: 'Shoppclass',
    data () {
        return {
            activeName: '0',
            dialogVisible: true
        }
    },
    inject: ['tablesThis'],
    methods: {
        handleClick(tab, event){
            this.activeName = tab.index
        }
    }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
._shopClass /deep/ .el-dialog__wrapper .el-dialog{
    @include wh(vw(1160), vw(720));
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    .el-dialog__header{
        @include wh(100%, vw(60));
        @include bg-defaul($bg-color);
        box-sizing: border-box;
        padding-top: 0;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        .el-dialog__title{
            color: #ffffff;
            font-size: vw(16);
        }
        .el-dialog__headerbtn{
            top: vw(20);
            .el-dialog__close{
                color: #ffffff;
            }
        }
        
    }
    .el-dialog__body{
        flex:1;
        overflow: auto;
        padding-top: 0;
        .body-section{
            height: 100%;
        }
        .el-tabs .el-tabs__header .el-tabs__nav-wrap .el-tabs__nav-scroll .el-tabs__nav {
            .el-tabs__item{
              font-size:vw(16);
            }
            .el-tabs__active-bar{
                width: vw(20)!important;
                left: vw(25);
                @include bg-defaul($bg-color);
            }
            .is-active{
                @include fontColor-defaul($bg-color);
            }
        }
        .el-tabs__content .el-tab-pane{
            display: flex;
            .el-tab-section-left{
                width: vw(660);
                height: 100%;
                margin-right: vw(50);
                .class-margin-right{
                    margin-right: vw(30)
                }
                .label-dis-flex{
                    display: flex!important;
                    align-items: center;
                    .el-textarea{
                        flex: 1;
                        .el-textarea__inner{
                            height: vw(100);
                            resize: none;
                        }
                    }
                }
                .label-drop-down{
                    // display: flex;
                    margin-bottom: vw(30);
                    display: inline-block;
                    .label-drop-down-title{
                        font-size: vw(16);
                        color: #333333;
                        margin-right: vw(13);
                    }
                     .el-input {
                        flex:1;
                        .el-input__inner{
                            height: vw(40);
                        } 
                     }
                    /deep/ .el-select .el-input--suffix,.el-date-editor--date{
                        width: vw(210);
                        .el-input__inner{
                            height: vw(40);
                        }
                        .el-input__prefix .el-icon-date{
                            line-height: vw(40);
                        }
                    }
                    .el-input-group--append{
                        width: vw(210);
                        .el-input__inner{
                            height: vw(40);
                            font-size: vw(16);
                        }
                        .el-input-group__append{
                            width: vw(15);
                            background-color: #e7eaec;
                            font-size: vw(16);
                            color: #666666;
                        }
                    }
                }
            }
            .el-tab-section-right{
              flex: 1;
              .el-tab-section-label{
                display: flex;
                .personnel{
                    font-size: vw(16);
                    color: #333333;
                    em{
                        color: #666666;
                    }
                }
                .personnel:nth-child(1){
                    margin-right: vw(38);
                }
                .el-tab-section-label-title{
                    color: #333333;
                    font-size: vw(16);
                    margin-right: vw(30);
                }
                .radio_div{
                    flex: 1;
                    font-size: vw(16);
                    color: #999999;
                    .radio_section{
                        display: flex;
                        align-items: center;
                        margin-bottom: vw(20);
                        input[type=radio]{
                            @include wh(vw(14),vw(14));
                            border-radius: 100%;
                            width: 14px;
                            height: 14px;
                            background: #fff;
                            -webkit-appearance:none;
                            border: 1px solid #c9c9c9;
                            outline: none;
                            margin-right: vw(9);
                        }
                        input[type=radio]:checked{
                            background-size: 100%;
                            background: url("../../../../../../static/icon/icon_oselected.png")no-repeat center;
                        }
                        input[type=checkbox]{
                            @include wh(vw(14),vw(14));
                            border-radius: 2px;
                            width: 14px;
                            height: 14px;
                            background: #fff;
                            -webkit-appearance:none;
                            border: 1px solid #c9c9c9;
                            outline: none;
                            margin-right: vw(9);
                        }
                        input[type=checkbox]:checked{
                            background-size: 100%;
                            background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
                        }
                        ._input_append{
                            flex: 1;
                            margin-left: vw(31);
                            .el-input-group--append {
                                
                                .el-input__inner{
                                    height: vw(40);
                                }
                                .el-input-group__append{
                                    width: vw(15);
                                    background: #e7eaec;
                                    color: #666666;
                                }
                            }
                        }
                    }
                }
              }
              .el-tab-section-append{
                align-items: center;
                margin-top: vw(32);
                .el-tab-section-title{
                    font-size: vw(16);
                    color: #333333;
                    margin-right: vw(13);
                }
                .el-input-group--append{
                    flex: 1;
                    width: vw(300);
                    .el-input-group__append{
                        font-size: vw(16);
                        color: #666666;
                        background: #e7eaec;
                    }
                    .el-input__inner{
                        height: vw(40);
                    }
                }
                .el-select .el-input .el-input__inner{
                    height: vw(40);
                }
              }
              .el-tab-margin-left{
                  margin-left: vw(8);
              }
          }
        }
    }
    .el-dialog__footer{
      @include wh(100%, vw(80));
      .dialog-footer {
        button{
            width: vw(120);
        }
        .el-button--primary {
            @include bg-defaul($bg-color);
            @include borderColor-defaul($bg-color);
        }
      }
    }
}
</style>
