<template>
    <div class='table_conenter'>
        <topBtn></topBtn>
         <section class='div_gulptable'>
            <el-table
                :data="tableData"
                height="520"
                border
                stripe
                resizable=false
                style="width: 100%">
                <el-table-column
                    prop="date"
                    show-overflow-tooltip
                    align='center'
                    min-width='6.66%'
                    label="序号">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="date"
                    align='center'
                    min-width='6.66%'
                    label="姓名">
                </el-table-column>
                <el-table-column
                    prop="date"
                    show-overflow-tooltip
                    align='center'
                    min-width='11.11%'
                    label="手机">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="date"
                    align='center'
                    min-width='11.11%'
                    label="课程">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="date"
                    align='center'
                    min-width='6.66%'
                    label="教师">
                </el-table-column>
                <el-table-column
                    prop="date"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="已上|已排">
                </el-table-column>
                <el-table-column
                    prop="date"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="剩余|已购">
                </el-table-column>
                <el-table-column
                    prop="date"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="备注">
                </el-table-column>
                <el-table-column
                    label="更多操作"
                    min-width='31.11%'
                    show-overflow-tooltip>
                    <template  class='erp-call'>
                        <div class="grid-content bg-purple">
                            <label>
                                <el-button @click.native='class_tableClick'  type="text" size="small" class='erp-button-icon'>
                                    <i class='blue_three_icon icon-width'></i>
                                    课表
                                </el-button>
                                <el-button type="text"  size="small"  class='erp-button-icon'>
                                    <i class='book_icon icon-width '></i>
                                    排课
                                </el-button>
                            </label>
                            <label>
                                <el-button type="text"  size="small" @click.native='shopClassClick' class='erp-button-icon'>
                                    <i class='shop_icon icon-width '></i>
                                    购课
                                </el-button>
                                <el-button type="text"  size="small"  class='erp-button-icon'>
                                    <i class='shop_class_icon icon-width '></i>
                                    购物
                                </el-button>
                            </label>
                            <label>
                                <el-button type="text"  size="small"  class='erp-button-icon'>
                                    <!-- <i class='classroom_icon icon-width '></i> -->
                                    充值
                                </el-button>
                            </label>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </section>
        <addStudentModel></addStudentModel>
        <transition name='stu'>
            <shoppingClassInfo v-show='shoppclassInfoShow'></shoppingClassInfo> 
        </transition>
        <shoppClass ></shoppClass>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import {studentList} from '../../../../getserver/server'
import {mapState} from 'vuex'
import topBtn from './topBtn/topBtn'
import addStudentModel from './addStudents/addStudent'
import shoppingClassInfo from './shopp_classinfo/shopclass_info'
import shoppClass from './shopp_class/shopp_class'
export default {
  name: 'tables',
  data () {
    return {
      shoppclassInfoShow: false,
      addStudentShow: false,
      shopClassShos:false,
      tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄'
        }]
    }
  },
  provide: function(){
    return {
      tablesThis: this
    }
  },
  created () {
    this.getStudentlist()
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    layerOpen:function (a,b) {
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    },
    class_tableClick () {
      // 课表点击
      this.shoppclassInfoShow = !this.shoppclassInfoShow
    },
    shopClassClick () {
      // 购课点击
      this.shopClassShos = !this.shopClassShos
    },
    async getStudentlist () {
      let resDate = await studentList(this.userLoginInfo.orgid, '')
    }
  },
  components:{
    addStudentModel: addStudentModel,
    topBtn: topBtn,
    shoppingClassInfo: shoppingClassInfo,
    shoppClass: shoppClass
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
@import '../../../../style/var';
.stu-enter,
.stu-leave-to,
.stu-enter-to,
.stu-leave{
    transition: all 0.3s ease;
}
.stu-enter,
.stu-leave-to{
  opacity: 0;
}
.stu-enter-to,
.stu-leave{
    opacity: 1;
}

.div_gulptable{
    @include wh(100%,auto);
}
.div_gulptable /deep/ .el-table {
    height: 540px;
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
    .el-table__body-wrapper {
        @include bgColor-defaul($bgColor-color)
    }
}
.grid-content{
    text-align: center;
    display: flex;
    padding: 0 35px;
    box-sizing: border-box;
    label{
      width: 50%;
      display: flex;
      /deep/ .el-button span{
        @include fontColor-defaul($fn-color)
      }
      .icon-width{
            @include wh(20px, 20px);
            display: inline-block;
            margin-right: vw(6);
            font-size: 16px;
            color: #666666;
        }
    }
    button:nth-child(2n){
        margin-right: vw(30);
        margin-left: vw(30);
    }
}
</style>
